<template>
<div>
  <h3>{{ id }}</h3>
  <div class="list-group">
    <a
      v-for="script in scripts"
      :key="script"
      :id="script"
      class="list-group-item list-group-item-action"
    >
    <router-link :to="{name: 'Script', params: {pid:id, id:script}}">
      <label class="test-label"> {{ script }} </label>
    </router-link>
    </a>
  </div>
</div>
</template>

<script>
export default {
  name: 'Project',
  props: ['url','openfaasUrl', 'socket'],
  data() {
    return {
      id: this.$route.params.id,
      scripts: [],
    };
  },
  mounted(){
      fetch(this.url, { method: 'POST', body: JSON.stringify({ command: 4 , project_name:this.id}) }).then(data => data.json()).then(data => {
        if (data.success) {
            this.scripts = data.locust_scripts
        }
    }).catch(function(){
        //showInfo('Could not connect to server','red');
    })
  }
};
</script>

