<template>
  <div>
    <div class="btn-container">
      <button
        type="button"
        class="btn btn-primary"
        data-mdb-toggle="modal"
        data-mdb-target="#Modal"
        id="add-btn-Modal"
        :disabled="loading"
      >
        Add a new project
      </button>
      <button
        type="button"
        class="btn btn-danger"
        id="delete-project"
        :disabled="!enableDelete"
        @click="deleteProjects"
      >
        Delete
      </button>
    </div>
    <div class="list-group">
      <a
        v-for="project in projects"
        :key="project"
        class="list-group-item list-group-item-action"
      >
        <div class="form-check">
          <input
            class="form-check-input"
            type="checkbox"
            :value="project"
            v-model="markedProjects"
            :id="project"
          />
          <router-link :to="{name: 'Project', params: {id:project}}">
          <label class="test-label">{{ project }}</label>
          </router-link>
        </div>
      </a>
    </div>
    <!-- Modal -->
    <div
      class="modal fade"
      id="Modal"
      tabindex="-1"
      aria-labelledby="ModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="ModalLabel">Add a new Project</h5>
            <button
              type="button"
              class="btn-close"
              data-mdb-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body">
            <form>
              <div class="form-outline mb-4">
                <input
                  type="file"
                  webkitdirectory
                  mozdirectory
                  id="project-input"
                  ref="files"
                />
              </div>

              <!-- Submit button -->
              <button
                type="button"
                class="btn btn-primary btn-block"
                id="add-btn"
                data-mdb-dismiss="modal"
                @click="upload"
              >
                Add
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
    <div class="btn-container">
      <div
        v-if="loading"
        class="spinner-border text-primary spinner"
        id="spinner"
      ></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Home',
  props: ['url','openfaasUrl', 'socket'],
  data() {
    return {
      projects: [],
      loading: false,
      files: null,
      socketIntv: null,
      markedProjects: [],
    };
  },
  methods: {
    upload() {
      this.loading = true;
      const files = this.$refs.files.files;
      if (files.length < 1) {
        //showInfo('Please select a directory to upload','red');
        return false;
      }
      var data = new FormData();
      for (var i = 0; i < files.length; i++) {
        data.append("file" + i, files[i]);
      }
      fetch(this.url, { method: "POST", body: data })
        .then((data) => data.json())
        .then((data) => {
          console.log(data);
          if (data.success) {
            const task_id = data.task_id;
            this.socketIntv = setInterval(() => {
              this.socket.emit("task_stats", {
                project_name: task_id,
                openfaasurl: this.openfaasUrl,
              });
            }, 1000);
            this.socket.on(task_id, (message) => {
              // console.log(message);
              const data = JSON.parse(message.data);
              if (!data.success) {
                console.log("Something went wrong");
                this.loading = false;
                //showInfo('Something went wrong','red');
                clearInterval(this.socketIntv);
              } else if (data.status_code === 3) {
                console.log("thread is locking");
              } else if (data.status_code === 2) {
                console.log("installing project");
              } else if (data.status_code === 1) {
                console.log("installing failed");
                clearInterval(this.socketIntv);
                this.loading = false;
                //showInfo('Installation failed','red', message.error.replaceAll('\n', '<br />'));
              } else {
                console.log("Task is finished");
                clearInterval(this.socketIntv);
                this.$router.push({name: 'Project', params: {id:task_id}})
              }
            });
          } else {
            this.loading = false;
            // showInfo(data.message,'red');
          }
        })
        .catch((e) => {
          this.loading = false;
          // showInfo('Something went wrong','red');
        });
      return false;
    },
    deleteProjects() {
      //setConfirmationModal("Are you sure you want to delete these projects?",);
      fetch(this.url, {
        method: "POST",
        body: JSON.stringify({ command: 10, names: this.markedProjects }),
      })
        .then((data) => data.json())
        .then(() => {
          this.projects = this.projects.filter(project => !this.markedProjects.includes(project))
          this.markedProjects = []
        })
        .catch(function () {
          //showInfo("Could not connect to server", "red");
        });
      return false;
    },
  },
  computed: {
    enableDelete() {
      return this.markedProjects.length > 0
    },
  },
  mounted() {
    fetch(this.url, { method: "POST", body: JSON.stringify({ command: 3 }) })
      .then((data) => data.json())
      .then((data) => {
        if (data.success) {
          this.projects = data.projects;
          console.log(data);
        }
      })
      .catch(function () {
        //showInfo('Could not connect to server','red');
      });
  },
};
</script>
